import Particle, { getParticles } from "./particle";
import p5 from "p5";

export type WindowOffset = {
  x: number;
  y: number;
};

class ParticleSystem {
  particles: Array<Particle>;
  collision: boolean;
  p5: p5;
  collider: Particle;
  image: any;
  offset: WindowOffset;

  constructor(p5: p5, offset: WindowOffset) {
    this.particles = getParticles(6, 7, p5, offset);
    this.p5 = p5;
    this.collision = true;
    this.image = p5.loadImage("../../../pictures/meFace3.png");
    this.offset = offset;
  }

  setParticles(amount: number) {
    const diff = amount - this.particles.length;
    if (diff > 0) {
      this.particles = [
        ...this.particles,
        ...getParticles(diff, 7, this.p5, this.offset),
      ];
    } else if (diff < 0) {
      this.particles = this.particles.slice(0, diff);
    }
  }

  checkColision(particleA: Particle, particleB: Particle) {
    const distance = particleA.position.dist(particleB.position) * 2;
    const collided = distance <= particleA.dimension.width;

    if (collided) {
      particleA.velocity = this.p5.createVector(
        particleA.velocity.x * -Math.random() * 10 - particleB.velocity.x,
        particleA.velocity.y * -Math.random() * 10 - particleB.velocity.y
      );
    }
  }

  update() {
    this.particles.map((particle, i) => {
      particle.update(this.p5.mouseX, this.p5.mouseY);
      const otherParticles = [
        ...this.particles.slice(0, i),
        ...this.particles.slice(i + 1, this.particles.length),
      ];
      if (this.collision)
        otherParticles.map((other) => this.checkColision(particle, other));
      particle.draw();
    });
    if (this.collider) {
      console.log(
        "COLIDER POS",
        this.collider.position.x,
        this.collider.position.y
      );
      this.collider.drawCollider(this.p5.mouseX, this.p5.mouseY, this.image);
    }
  }
}

export default ParticleSystem;
